.app {
  padding: 20px;
}

h2 {
  margin-bottom: 6px;
}

button {
  font-size: 1.2rem;
  margin-right: 6px;
  margin-bottom: 6px;
}

button.selected {
  background-color: aqua;
}

ol li span {
  text-decoration: underline;
  cursor: pointer;
}

.selected-activity,
.selected-activity .background,
.selected-activity .modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.selected-activity .background {
  background-color: #0006;
  cursor: pointer;
}

.selected-activity .modal {
  bottom: 0;
  right: 0;
  margin: auto;
  width: 800px;
  height: min-content;
  max-height: 90%;
  background-color: #fff;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.selected-activity .modal .modal-content {
  overflow-y: scroll;
}

.selected-activity .modal .tags {
  padding: 0;
}
.selected-activity .modal .tags li {
  display: inline-block;
  background-color: #0002;
  padding: 2px 8px;
  margin-right: 8px;
  margin-bottom: 5px;
  border-radius: 5px;
}

.selected-activity .modal .json {
  color: #0003;
}

.select-random-activity-button {
  background-color: transparent;
  border: none;
  position: fixed;
  right: 0px;
  bottom: 20px;
  cursor: pointer;
}
.select-random-activity-button img {
  width: 200px;
}